.registerheading {
    background-color: rgb(39, 40, 112);
    color: white;
    display: flex;
    flex-direction: column;
    width: fit-content;
    margin: auto;
    border-radius: 10px;
    padding: 10px;
    margin-top: 25px;
    text-align: center;
}

.reghead2 h2 {
    text-align: center;
    margin-bottom: 48px;
}

.container {
    margin: 32px 70px;
    padding: 28px;
    border-radius: 10px;
}

.container button {
    margin-top: 29px;
}

.btnn {
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    border-radius: .25rem;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    display: flex;
    margin: auto;
}

@media(max-width:460px) {
    .reghead h1 {
        font-size: 19px;
    }

    .maindiv {
        font-size: 11px;
    }
}

@media(max-width:430px) {
    form {
        font-size: 10px;
    }

    form ::placeholder {
        font-size: 10px;
    }
}

@media (max-width:500px){
    .registerheading{
        margin-top: 80px;
    }
}